html {
  width: 100%;
  height: 100%;
  background: white;
}

body {
  width: 100%;
  height: 100%;
  min-width: $min-width;
  min-height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  font: 18px/25px $font-primary;
  color: black;
  background: white;
  -webkit-font-smoothing: antialiased;
  @include media-breakpoint-down(xxs) {
    font-size: 16px;
    line-height: 22px;
  }
  @include media-breakpoint-up(xl) {
    font-size: 20px;
    line-height: 30px;
  }
}

p {
  margin-bottom: 20px;
}

a {
  color: black;
  text-decoration: underline;
  transition: color $effect;
  &:hover, &:focus {text-decoration: none;}

  &.red {
    color: $color-red;
    text-decoration: none;
    &:hover, &:focus {
      color: black;
      text-decoration: none;
    }
  }
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
}
h1, .h1 {
  font-size: 35px;
  line-height: 38px;
  font-weight: 800;
  @include media-breakpoint-up(xl) {
    font-size: 60px;
    line-height: 68px;
  }
}
h3, .h3 {
  margin-bottom: 20px;
  font-weight: 800;
  @include media-breakpoint-up(xl) {
    font-size: 36px;
    line-height: 55px;
  }
}
h4, .h4 {
  margin-bottom: 16px;
  font-weight: bold;
  @include media-breakpoint-up(xl) {
    font-size: 32px;
    line-height: normal;
  }
}
h5, .h5 {
  font-weight: bold;
}

.color-red {
  color: $color-red;
}